#toast-container {
	display: block;
	position: fixed;
	z-index: 10000;

	min-width: 100%;
	bottom: 0%;
}

.toast {
	@extend .z-depth-1;
	border-radius: 2px;
	top: 35px;
	width: auto;
	margin-top: 10px;
	position: relative;
	max-width: 100%;
	height: auto;
	min-height: $toast-height;
	line-height: 1.5em;
	background-color: $toast-color;
	padding: 10px 25px;
	font-size: 1.1rem;
	font-weight: 300;
	color: $toast-text-color;
	display: flex;
	align-items: center;
	justify-content: space-between;
	cursor: default;

	.toast-action {
		color: $toast-action-color;
		font-weight: 500;
		margin-right: -25px;
		margin-left: 3rem;
	}

	&.rounded {
		border-radius: 24px;
	}

	@media #{$small-and-down} {
		width: 100%;
		border-radius: 0;
	}
}