/* Checkboxes
   ========================================================================== */

/* Remove default checkbox */
[type="checkbox"]:not(:checked),
[type="checkbox"]:checked {
	position: absolute;
	opacity: 0;
	pointer-events: none;
}

// Checkbox Styles
[type="checkbox"] {

	// Text Label Style
	+span:not(.lever) {
		position: relative;
		padding-left: 35px;
		cursor: pointer;
		display: inline-block;
		height: 25px;
		line-height: 25px;
		font-size: 1rem;
		user-select: none;
	}

	/* checkbox aspect */
	+span:not(.lever):before,
	&:not(.filled-in)+span:not(.lever):after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 18px;
		height: 18px;
		z-index: 0;
		border: 2px solid $radio-empty-color;
		border-radius: 1px;
		margin-top: 3px;
		transition: .2s;
	}

	&:not(.filled-in)+span:not(.lever):after {
		border: 0;
		transform: scale(0);
	}

	&:not(:checked):disabled+span:not(.lever):before {
		border: none;
		background-color: $input-disabled-color;
	}

	// Focused styles
	&.tabbed:focus+span:not(.lever):after {
		transform: scale(1);
		border: 0;
		border-radius: 50%;
		box-shadow: 0 0 0 10px rgba(0, 0, 0, .1);
		background-color: rgba(0, 0, 0, .1);
	}
}

[type="checkbox"]:checked {
	+span:not(.lever):before {
		top: -4px;
		left: -5px;
		width: 12px;
		height: 22px;
		border-top: 2px solid transparent;
		border-left: 2px solid transparent;
		border-right: $radio-border;
		border-bottom: $radio-border;
		transform: rotate(40deg);
		backface-visibility: hidden;
		transform-origin: 100% 100%;
	}

	&:disabled+span:before {
		border-right: 2px solid $input-disabled-color;
		border-bottom: 2px solid $input-disabled-color;
	}
}

/* Indeterminate checkbox */
[type="checkbox"]:indeterminate {
	+span:not(.lever):before {
		top: -11px;
		left: -12px;
		width: 10px;
		height: 22px;
		border-top: none;
		border-left: none;
		border-right: $radio-border;
		border-bottom: none;
		transform: rotate(90deg);
		backface-visibility: hidden;
		transform-origin: 100% 100%;
	}

	// Disabled indeterminate
	&:disabled+span:not(.lever):before {
		border-right: 2px solid $input-disabled-color;
		background-color: transparent;
	}
}

// Filled in Style
[type="checkbox"].filled-in {

	// General
	+span:not(.lever):after {
		border-radius: 2px;
	}

	+span:not(.lever):before,
	+span:not(.lever):after {
		content: '';
		left: 0;
		position: absolute;
		/* .1s delay is for check animation */
		transition: border .25s, background-color .25s, width .20s .1s, height .20s .1s, top .20s .1s, left .20s .1s;
		z-index: 1;
	}

	// Unchecked style
	&:not(:checked)+span:not(.lever):before {
		width: 0;
		height: 0;
		border: 3px solid transparent;
		left: 6px;
		top: 10px;
		transform: rotateZ(37deg);
		transform-origin: 100% 100%;
	}

	&:not(:checked)+span:not(.lever):after {
		height: 20px;
		width: 20px;
		background-color: transparent;
		border: 2px solid $radio-empty-color;
		top: 0px;
		z-index: 0;
	}

	// Checked style
	&:checked {
		+span:not(.lever):before {
			top: 0;
			left: 1px;
			width: 8px;
			height: 13px;
			border-top: 2px solid transparent;
			border-left: 2px solid transparent;
			border-right: 2px solid $input-background;
			border-bottom: 2px solid $input-background;
			transform: rotateZ(37deg);
			transform-origin: 100% 100%;
		}

		+span:not(.lever):after {
			top: 0;
			width: 20px;
			height: 20px;
			border: 2px solid $primary-color-light;
			background-color: $primary-color-light;
			z-index: 0;
		}
	}

	// Focused styles
	&.tabbed:focus+span:not(.lever):after {
		border-radius: 2px;
		border-color: $radio-empty-color;
		background-color: rgba(0, 0, 0, .1);
	}

	&.tabbed:checked:focus+span:not(.lever):after {
		border-radius: 2px;
		background-color: $secondary-color;
		border-color: $secondary-color;
	}

	// Disabled style
	&:disabled:not(:checked)+span:not(.lever):before {
		background-color: transparent;
		border: 2px solid transparent;
	}

	&:disabled:not(:checked)+span:not(.lever):after {
		border-color: transparent;
		background-color: $input-disabled-solid-color;
	}

	&:disabled:checked+span:not(.lever):before {
		background-color: transparent;
	}

	&:disabled:checked+span:not(.lever):after {
		background-color: $input-disabled-solid-color;
		border-color: $input-disabled-solid-color;
	}
}