.materialboxed {
	&:hover {
		&:not(.active) {
			opacity: .8;
		}
	}

	display: block;
	cursor: zoom-in;
	position: relative;
	transition: opacity .4s;
	-webkit-backface-visibility: hidden;

	&.active {
		cursor: zoom-out;
	}
}

#materialbox-overlay {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #292929;
	z-index: 1000;
	will-change: opacity;
}

.materialbox-caption {
	position: fixed;
	display: none;
	color: #fff;
	line-height: 50px;
	bottom: 0;
	left: 0;
	width: 100%;
	text-align: center;
	padding: 0% 15%;
	height: 50px;
	z-index: 1000;
	-webkit-font-smoothing: antialiased;
}