// 1. Colors
// ==========================================================================

@function darkValue($dark, $default) {
	@if $theme =="dark" {
		@return $dark;
	}

	@return $default;
}

$pure_black: color("shades", "black") !default;
$pure_white: color("shades", "white") !default;
$theme: "";

$primary-color: color("deep-purple", "base") !default;
$primary-color-light: lighten($primary-color, 15%) !default;
$primary-color-dark: darken($primary-color, 15%) !default;

$secondary-color: color("teal", "lighten-1") !default;
$success-color: color("green", "base") !default;
$error-color: color("red", "base") !default;
$link-color: color("light-blue", "darken-1") !default;

$background-color-light: #DFDFDF !default;
$background-color-dark: #1F1F1F !default;
$background-color: darkValue($background-color-dark, $background-color-light) !default;

$text-color-light: #DFDFDF !default;
$text-color-dark: #1F1F1F !default;
$text-color: darkValue($text-color-light, $text-color-dark) !default;

// 2. Badges
// ==========================================================================

$badge-bg-color: $primary-color-light  !default;
$badge-height: 22px !default;


// 3. Buttons
// ==========================================================================

// Shared styles
$button-border: none !default;
$button-background-focus: lighten($secondary-color, 4%) !default;
$button-font-size: 14px !default;
$button-icon-font-size: 1.3rem !default;
$button-height: 36px !default;
$button-padding: 0 16px !default;
$button-radius: 2px !default;

// Disabled styles
$button-disabled-background: #DFDFDF !default;
$button-disabled-color: #9F9F9F !default;

// Raised buttons
$button-raised-background: $primary-color  !default;
$button-raised-background-hover: lighten($button-raised-background, 5%) !default;
$button-raised-color: #fff !default;

// Large buttons
$button-large-font-size: 15px !default;
$button-large-icon-font-size: 1.6rem !default;
$button-large-height: $button-height * 1.5 !default;
$button-floating-large-size: 56px !default;

// Small buttons
$button-small-font-size: 13px !default;
$button-small-icon-font-size: 1.2rem !default;
$button-small-height: $button-height * .9 !default;
$button-floating-small-size: $button-height * .9 !default;

// Flat buttons
$button-flat-color: $text-color  !default;
$button-flat-disabled-color: lighten(#999, 10%) !default;

// Floating buttons
$button-floating-background: $secondary-color  !default;
$button-floating-background-hover: $button-floating-background  !default;
$button-floating-color: #fff !default;
$button-floating-size: 40px !default;
$button-floating-radius: 50% !default;


// 4. Cards
// ==========================================================================

$card-padding: 24px !default;
$card-bg-color: lighten($background-color, 5%) !default;
$card-link-color: $primary-color-light  !default;
$card-link-color-light: lighten($card-link-color, 20%) !default;


// 5. Carousel
// ==========================================================================

$carousel-height: 400px !default;
$carousel-item-height: $carousel-height / 2 !default;
$carousel-item-width: $carousel-item-height  !default;


// 6. Collapsible
// ==========================================================================

$collapsible-height: 3rem !default;
$collapsible-line-height: $collapsible-height  !default;
$collapsible-header-color: $background-color  !default;
$collapsible-border-color: lighten($background-color, 50%) !default;
$collapsible-active-color: lighten($background-color, 10%) !default;


// 7. Chips
// ==========================================================================

$chip-bg-color: #e4e4e4 !default;
$chip-border-color: #9e9e9e !default;
$chip-selected-color: #26a69a !default;
$chip-margin: 5px !default;


// 8. Date + Time Picker
// ==========================================================================

$datepicker-display-font-size: 2.8rem;
$datepicker-calendar-header-color: #999;
$datepicker-weekday-color: rgba(0, 0, 0, .87) !default;
$datepicker-weekday-bg: darken($secondary-color, 7%) !default;
$datepicker-date-bg: $secondary-color  !default;
$datepicker-year: rgba(255, 255, 255, .7) !default;
$datepicker-focus: rgba(0, 0, 0, .05) !default;
$datepicker-selected: $secondary-color  !default;
$datepicker-selected-outfocus: desaturate(lighten($secondary-color, 35%), 15%) !default;
$datepicker-day-focus: transparentize(desaturate($secondary-color, 5%), .75) !default;
$datepicker-disabled-day-color: rgba(0, 0, 0, .3) !default;

$timepicker-clock-color: $text-color  !default;
$timepicker-clock-shadow-color: $background-color  !default;
$timepicker-clock-plate-bg: $background-color  !default;


// 9. Dropdown
// ==========================================================================

$dropdown-bg-color: lighten($background-color, 5%) !default;
$dropdown-hover-bg-color: lighten($background-color, 10%) !default;
$dropdown-color: $primary-color-light  !default;
$dropdown-item-height: 50px !default;


// 10. Forms
// ==========================================================================

// Text Inputs + Textarea
$input-height: 3rem !default;
$input-border-color: color("grey", "base") !default;
$input-border: 1px solid $input-border-color  !default;
$input-background: #fff !default;
$input-error-color: $error-color  !default;
$input-success-color: $success-color  !default;
$input-focus-color: $primary-color-light  !default;
$input-font-size: 16px !default;
$input-margin-bottom: 8px;
$input-margin: 0 0 $input-margin-bottom 0 !default;
$input-padding: 0 !default;
$label-font-size: .8rem !default;
$input-disabled-color: darken($text-color, 40%) !default;
$input-disabled-solid-color: #949494 !default;
$input-disabled-border: 1px dotted $input-disabled-color  !default;
$input-invalid-border: 1px solid $input-error-color  !default;
$input-icon-size: 2rem;
$placeholder-text-color: lighten($input-border-color, 20%) !default;

// Radio Buttons
$radio-fill-color: $primary-color-light  !default;
$radio-empty-color: #5a5a5a !default;
$radio-border: 2px solid $radio-fill-color  !default;

// Range
$range-height: 14px !default;
$range-width: 14px !default;
$track-height: 3px !default;

// Select
$select-border: 1px solid #f2f2f2 !default;
$select-background: rgba(255, 255, 255, 0.90) !default;
$select-focus: 1px solid lighten($secondary-color, 47%) !default;
$select-option-hover: rgba(0, 0, 0, .08) !default;
$select-option-focus: rgba(0, 0, 0, .08) !default;
$select-option-selected: rgba(0, 0, 0, .03) !default;
$select-padding: 5px !default;
$select-radius: 2px !default;
$select-disabled-color: rgba(0, 0, 0, .3) !default;

// Switches
$switch-bg-color: $primary-color  !default;
$switch-checked-lever-bg: desaturate(lighten($switch-bg-color, 25%), 25%) !default;
$switch-unchecked-bg: darkValue($text-color-light, lighten($text-color-light, 15%)) !default;
$switch-unchecked-lever-bg: darkValue(darken($background-color-light, 50%), lighten($background-color-dark, 50%)) !default;
$switch-radius: 15px !default;


// 11. Global
// ==========================================================================

// Media Query Ranges
$small-screen-up: 601px !default;
$medium-screen-up: 993px !default;
$large-screen-up: 1201px !default;
$small-screen: 600px !default;
$medium-screen: 992px !default;
$large-screen: 1200px !default;

$medium-and-up: "only screen and (min-width : #{$small-screen-up})" !default;
$large-and-up: "only screen and (min-width : #{$medium-screen-up})" !default;
$extra-large-and-up: "only screen and (min-width : #{$large-screen-up})" !default;
$small-and-down: "only screen and (max-width : #{$small-screen})" !default;
$medium-and-down: "only screen and (max-width : #{$medium-screen})" !default;
$medium-only: "only screen and (min-width : #{$small-screen-up}) and (max-width : #{$medium-screen})" !default;


// 12. Grid
// ==========================================================================

$num-cols: 12 !default;
$gutter-width: 1.5rem !default;
$element-top-margin: $gutter-width/3 !default;
$element-bottom-margin: (
	$gutter-width*2)/3 !default;


// 13. Navigation Bar
// ==========================================================================

$navbar-height: 64px !default;
$navbar-line-height: $navbar-height  !default;
$navbar-height-mobile: 56px !default;
$navbar-line-height-mobile: $navbar-height-mobile  !default;
$navbar-font-size: 1rem !default;
$navbar-font-color: #fff !default;
$navbar-brand-font-size: 2.1rem !default;


// 14. Side Navigation
// ==========================================================================

$sidenav-width: 300px !default;
$sidenav-font-size: 14px !default;
$sidenav-font-color: $text-color  !default;
$sidenav-subheader-color: lighten($primary-color, 15%) !default;
$sidenav-bg-color: $background-color  !default;
$sidenav-padding: 16px !default;
$sidenav-item-height: 48px !default;
$sidenav-line-height: $sidenav-item-height  !default;


// 15. Photo Slider
// ==========================================================================

$slider-bg-color: color('grey', 'base') !default;
$slider-bg-color-light: color('grey', 'lighten-2') !default;
$slider-indicator-color: color('green', 'base') !default;


// 16. Spinners | Loaders
// ==========================================================================

$spinner-default-color: $secondary-color  !default;


// 17. Tabs
// ==========================================================================

$tabs-underline-color: $primary-color-light  !default;
$tabs-text-color: darkValue(lighten($primary-color-light, 5%), $primary-color) !default;
$tabs-bg-color: darkValue(lighten($background-color, 10%), $background-color) !default;


// 18. Tables
// ==========================================================================

$table-border-color: darkValue($background-color-light, $background-color-dark) !default;
$table-striped-color: darkValue(lighten($background-color, 5%), darken($background-color, 5%)) !default;
$table-highlight-color: darkValue(lighten($background-color, 10%), darken($background-color, 10%)) !default;
$table-striped-highlight-color: darkValue(lighten($background-color, 15%), darken($background-color, 15%)) !default;


// 19. Toasts
// ==========================================================================

$toast-height: 48px !default;
$toast-color: #323232 !default;
$toast-text-color: #fff !default;
$toast-action-color: #eeff41;


// 20. Typography
// ==========================================================================

$font-stack: -apple-system,
	BlinkMacSystemFont,
	"Segoe UI",
	Roboto,
	Oxygen-Sans,
	Ubuntu,
	Cantarell,
	"Helvetica Neue",
	sans-serif !default;
// Header Styles
$h1-fontsize: 4.2rem !default;
$h2-fontsize: 3.56rem !default;
$h3-fontsize: 2.92rem !default;
$h4-fontsize: 2.28rem !default;
$h5-fontsize: 1.64rem !default;
$h6-fontsize: 1.15rem !default;


// 21. Footer
// ==========================================================================

$footer-font-color: #fff !default;
$footer-bg-color: $primary-color  !default;
$footer-copyright-font-color: $text-color  !default;
$footer-copyright-bg-color: darken($primary-color, 15%) !default;


// 22. Flow Text
// ==========================================================================

$range : $large-screen - $small-screen  !default;
$intervals: 20 !default;
$interval-size: $range / $intervals  !default;


// 23. Collections
// ==========================================================================

$collection-border-color: lighten($background-color, 50%) !default;
$collection-bg-color: $background-color  !default;
$collection-active-bg-color: $primary-color  !default;
$collection-active-color: lighten($text-color, 55%) !default;
$collection-hover-bg-color: lighten($background-color, 10%) !default;
$collection-link-color: $text-color  !default;
$collection-line-height: 1.5rem !default;


// 24. Progress Bar
// ==========================================================================

$progress-bar-color: $primary-color  !default;


// 25. Tooltip
// ==========================================================================

$tooltip-background-color: lighten($background-color, 15%) !default;
$tooltip-text-color: darken($text-color, 15%) !default;


// 26. Modal
// ==========================================================================

$modal-bg-color: $background-color  !default;
$modal-footer-bg-color: $background-color  !default;
$modal-overlay-color: darkValue(rgba(0, 0, 0, .125), rgba(0, 0, 0, .5)) !default;
$modal-blur: 10px !default;


// 27. Pagination
// ==========================================================================

$pagination-text-color: $text-color  !default;
$pagination-disabled-color: darkValue(darken($text-color, 60%), lighten($text-color, 60%)) !default;